<template>
  <div class="content">
    <!--  -->
    <div class="block">
      <el-row class="block-row">
        <el-tooltip
          class="box-item"
          effect="dark"
          content=""
          placement="bottom"
          :hide-after="0"
        >
          <el-icon
            class="icon"
            size="22px"
            color="rgb(137, 137, 137)"
            style="margin-left: 70px"
            @click="openDrawer()"
          >
            <CopyDocument />
          </el-icon>
        </el-tooltip>
      </el-row>
    </div>

    <div class="body-check">
      <div class="card">
        <el-row class="all_block">
          <el-col class="body_block">
            <el-col class="title"></el-col>
            <el-col class="detail">{{ currentMajorName }}</el-col>
          </el-col>
          <el-col>
            <el-col class="title"></el-col>
            <el-col class="detail">{{ currentYear }} </el-col>
          </el-col>
          <el-col> </el-col>
        </el-row>
      </div>
    </div>
  </div>
  <!--  -->
  <el-drawer v-model="drawer">
    <el-row>
      <el-col :span="14">
        <DrawerSearch msg="" @SearchValue="getSearchValue" />
      </el-col>
      <el-col :span="10">
        <el-select
          v-model="year"
          placeholder=""
          clearable
          @change="getCopyProgram()"
        >
          <el-option
            v-for="year in enroll_year"
            :key="year.dictValue"
            :label="year.dictLabel"
            :value="year.dictLabel"
          >
          </el-option>
        </el-select>
      </el-col>
    </el-row>
    <el-row style="margin-top: 40px">
      <el-table :data="programList" style="width: 100%" @row-click="copy">
        <el-table-column prop="majorName" label="" min-width="180px" />
        <el-table-column prop="enrollyear" label="" min-width="180px" />
      </el-table>
    </el-row>
  </el-drawer>
  <!--  -->
  <el-dialog v-model="dialogVisible" title="" width="400px">
    <el-row style="margin-bottom: 20px; font-size: 15px"
      >{{ currentRow.majorName }}{{ currentRow.enrollyear }} 
      </el-row
    >
    <el-checkbox-group v-model="checkList">
      <el-checkbox label=""></el-checkbox>
      <el-checkbox label=""></el-checkbox>
      <el-checkbox label=""></el-checkbox>
    </el-checkbox-group>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false"></el-button>
        <el-button type="primary" @click="confirm()"></el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import DrawerSearch from "@/components/general/drawerSearch.vue";
import { searchProgram, checkProgram } from "@/api/program";
import { getDictionary } from "@/api/dictionary";
import { copyProgram } from "@/api/basecourse";
import { ElMessage, ElMessageBox } from "element-plus";
import { addProgram } from "@/api/program";
import { List, CopyDocument } from "@element-plus/icons-vue";
export default {
  name: "Setting",
  components: {
    DrawerSearch,
    CopyDocument,
    List,
  },
  data() {
    return {
      programId: "",
      year: "",
      enroll_year: [],
      checkList: ["", "", ""],
      currentRow: [],
      programList: [],
      keyword: "",
      drawer: false,
      dialogVisible: false,
      currentMajorId: "",
      currentMajorName: "",
      departmentId: "",
      schoolId: "",
      currentYear: "",
      noProgram: Boolean,
    };
  },
  computed: {
    yearChange() {
      return this.$store.state.currentInfo.year;
    },
  },
  watch: {
    yearChange: {
      deep: true,
      handler(value) {
        this.currentYear = this.$store.state.currentInfo.year;
        this.checkProgram();
      },
    },
  },
  mounted() {
    this.currentMajorId = this.$store.state.major.majorId;
    this.currentMajorName = this.$store.state.major.majorName;
    this.currentYear = this.$store.state.currentInfo.year;
    this.departmentId = this.$store.state.currentInfo.departmentId;
    this.schoolId = this.$store.state.currentInfo.schoolId;
    this.getDictionary();
    this.checkProgram();
  },
  methods: {
    //
    getDictionary() {
      getDictionary().then((res) => {
        console.log("getDictionary", res);
        this.enroll_year = res.enroll_year;
      });
    },
    //
    checkProgram() {
      checkProgram(this.currentMajorId, this.currentYear)
        .then((res) => {
          console.log("checkProgram", res);
          this.programId = res.data.programId;
          this.noProgram = false;
        })
        .catch((e) => {
          if (e.code === "PROGRAM_NOT_FIND" && e.msg === "") {
            this.noProgram = true;
          }
        });
    },
    // 
    openDrawer() {
      this.drawer = true;
      // 
      this.getCopyProgram();
    },
    //
    getSearchValue(data) {
      this.keyword = data;
      this.getCopyProgram();
    },
    // 
    getCopyProgram() {
      searchProgram(
        this.schoolId,
        this.departmentId,
        this.keyword,
        this.year
      ).then((res) => {
        this.programList = res.rows;
        console.log(" ", this.programList);
      });
    },
    //
    copy(row) {
      console.log("copy", row);
      this.currentRow = row;
      // 
      this.dialogVisible = true;
    },
    // 
    confirm() {
      if (this.noProgram === false) {
        this.copyInfo();
      } else {
        ElMessageBox.confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        })
          .then(() => {
            this.addProgram();
          })
          .catch(() => {});
      }
    },
    //
    addProgram() {
      addProgram(
        this.currentMajorId,
        this.departmentId,
        this.schoolId,
        this.currentYear
      ).then((res) => {
        console.log("addProgram", res);
        checkProgram(this.currentMajorId, this.currentYear)
        .then((r) => {
          console.log("checkProgram", r);
          this.programId = r.data.programId;
          console.log("@", this.programId);
          this.noProgram = false;
          this.copyInfo()
        })
        .catch((e) => {
          if (e.code === "PROGRAM_NOT_FIND" && e.msg === "") {
            this.noProgram = true;
          }
        });
      });
    },
    //
    copyInfo() {
      let obj = {};
      obj.sourceProgramId = this.currentRow.programId;
      obj.targetProgramId = this.programId;
      // 
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyGraduateAttribute = 1;
      } else {
        obj.isCopyGraduateAttribute = 0;
      }
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyCourseSystem = 1;
      } else {
        obj.isCopyCourseSystem = 0;
      }
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyAchievement = 1;
      } else {
        obj.isCopyAchievement = 0;
      }
      copyProgram(obj).then((res) => {
        console.log("copyActAndObj", res);
        this.dialogVisible = false;
        if (res.code == "SUCCESS") {
          ElMessage({
            type: "success",
            message: ``,
            duration: 1500,
          });
          this.drawer = false;
        }
      });
    },
  },
};
</script>

