            style="margin-left: 15px"
            @click="toTemplate()"
          >
            <Management />
          </el-icon>
        </el-tooltip>
        <!--  -->
        <el-tooltip
          class="box-item"
          effect="dark"
          content=""
          placement="bottom"
          :hide-after="0"
        >
          <el-icon
            class="icon"
            size="22px"
