            style="margin-left: 20px"
            @click="openDrawer()"
          >
            <CopyDocument />
          </el-icon>
        </el-tooltip>
      </el-row>
    </div>
    <div class="body">
      <!-- <el-tabs class="tabs" type="border-card" v-model="activeName"> -->
      <!-- <el-tab-pane label="" name="first"> -->
      <div class="card">
        <h3></h3>
        <el-col>
          <el-col style="margin-top: 15px">
            <div class="detail-title"></div>
            <div class="detail-info" style="margin-top: 10px">
              {{ form.courseName }}
            </div>
          </el-col>
          <el-row style="margin-top: 15px">
            <el-col :span="12" style="margin-top: 10px">
              <div class="detail-title"></div>
              <div class="detail-info" style="margin-top: 10px">
                {{ form.courseCode }}
              </div>
            </el-col>
            <el-col :span="6" style="margin-top: 10px">
              <div class="detail-title"></div>
              <div class="detail-info" style="margin-top: 10px">
                {{ form.credit }}
              </div>
            </el-col>
          </el-row>
          <el-row style="margin-top: 15px">
            <el-col :span="12" style="margin-top: 10px">
              <div class="detail-title"></div>
              <div class="detail-info" style="margin-top: 10px">
                {{ form.courseNature }}
              </div>
            </el-col>
            <el-col :span="6" style="margin-top: 10px">
              <div class="detail-title"></div>
              <div class="detail-info" style="margin-top: 10px">
                {{ form.courseType }}
              </div>
            </el-col>
          </el-row>
          <el-col style="margin-top: 25px">
            <div class="detail-title"></div>
            <div class="detail-info" style="margin-top: 10px">
              <!-- {{ form.remark }} -->
            </div>
          </el-col>
          <el-col style="margin-top: 45px">
            <el-row>
              <el-col class="detail-title"></el-col>
              <el-col :span="6">
                <el-upload
                  v-show="!hasFile"
                  class="upload-demo"
                  :action="action"
                  :data="data"
                  :headers="headers"
                  accept=".doc,.docx,.pdf,.rar,.zip"
                  :file-list="fileList"
                  :limit="1"
                  show-file-list
                  name="files"
                  :on-success="uploadSuccess"
                  :on-exceed="uploadExceed"
                  :on-remove="uploadRemove"
                >
                  <el-button size="small" type="primary"
                    ></el-button
                  >
                </el-upload>
              </el-col>
              <el-col>
                <el-table
                  v-show="hasFile"
                  :data="filesList"
                  style="cursor: pointer; margin-top: 10px; width: 225px"
                  :show-header="false"
                  @row-click="downloadFile"
                >
                  <el-table-column
                    prop="originalName"
                    label=""
                    width="180px"
                  />
                  <el-table-column width="45px">
                    <template #default="scope">
                      <el-tooltip content="" :hide-after="0">
                        <el-button
                          link
                          style="color: #3f51b5"
                          @click.stop="beforeRemove(scope.row)"
                          ><el-icon><CircleClose /></el-icon
                        ></el-button>
                      </el-tooltip>
                    </template>
                  </el-table-column>
                </el-table>
              </el-col>
