    </div>
    <!--  -->
    <el-drawer v-model="drawer" :modal="true">
      <el-row>
        <el-col :span="14">
          <DrawerSearch
            msg=""
            @SearchValue="getSearchValue"
          />
        </el-col>
        <el-col :span="10">
          <el-select
            v-model="version"
            placeholder=""
            clearable
            @change="getEditDetail()"
          >
            <el-option
              v-for="version in versionList"
              :key="version.versionId"
              :label="version.versionName"
              :value="version.versionId"
            >
            </el-option>
          </el-select>
        </el-col>
      </el-row>
      <el-row style="margin-top: 40px">
        <el-table
          :data="copyCourseList"
          style="width: 100%"
          @row-click="copyDetail"
        >
          <el-table-column prop="courseName" label="" min-width="180px" />
          <el-table-column prop="courseCode" label="" min-width="180px" />
          <el-table-column prop="versionName" label="" min-width="180px" />
        </el-table>
      </el-row>
      <!--  -->
      <div class="pagination-container" flex>
        <div class="loadMore">
          <el-button link plain v-show="showLoadmore" @click="loadMore()"
            ></el-button
          >
        </div>
      </div>
    </el-drawer>

    <!--  -->
    <el-dialog v-model="dialogVisible" title="" width="400px">
      <el-row style="margin-bottom: 20px; font-size: 15px"
        >{{ currentRow.courseName }}{{
          currentRow.versionName
        }}</el-row
      >
      <el-checkbox-group v-model="checkList">
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
      </el-checkbox-group>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false"></el-button>
          <el-button type="primary" @click="copyInfo()"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import request from "@/utils/request/request";
import addBtn from "@/components/general/addBtn.vue";
import { ElTooltip, ElMessage, ElMessageBox } from "element-plus";
import {
  Back,
  Histogram,
  List,
  Checked,
  CircleClose,
  CopyDocument,
  Management,
  Download,
  DataAnalysis,
} from "@element-plus/icons-vue";
import Cookies from "js-cookie";
import {
  getObjectives,
  downloadDetail,
  getEditDetail,
  getVersion,
  getFilesList,
  copyActAndObj,
} from "@/api/basecourse";
import DrawerSearch from "@/components/general/drawerSearch.vue";
import { downloadFileId, downloadFile, removeFileId } from "@/api/common";
import { inject } from "vue";

export default {
  name: "baseCourseDetail",
  components: {
    DrawerSearch,
    CopyDocument,
    ElTooltip,
    ElMessage,
    ElMessageBox,
    addBtn,
    request,
    Back,
    Histogram,
    List,
    Checked,
    CircleClose,
    Management,
    Download,
    DataAnalysis,
  },
  data() {
    return {
      from: "",
      filesList: [],
      checkList: ["", "", ""],
      currentRow: {},
      copyCourseList: [],
      keyword: "",
      dialogVisible: false,
      versionList: [],
      version: "",
      drawer: false,
      //
      fileList: [],
      headers: {
        Authorization: "Bearer " + Cookies.get("Admin-Token"),
      },
      action: "",
      data: [],
      //
      indicators: [],
      departmentId: "",
      schoolId: "",
      majorId: [],
      majorNumber: 0,
      currentMajorId: "",
      //
      detailId: "",
      object: [],
      hasFile: false,
      courseId: "",
      //
      versionName: "",
      versionId: "",
      versionFlag: "",
      //basecouse
      form: {
        courseName: "",
        courseCode: "",
        courseType: "",
        courseNature: "",
        credit: "",
        courseYear: "",
        remark: "",
      },
      //majorinformation
      majorForm: [],
      pageSize: 20,
      pageNum: 1,
      total: 0,
      showLoadmore: false,
      url: "",
    };
  },
  mounted() {
    this.getRouter();
    this.url = inject("$baseURL");
    // sessionStorage.setItem("baseCourseSearchForm", this.form.courseName);
    this.$store.commit("course/setCourseName", this.form.courseName);
    this.$store.commit("course/setCourseId", this.courseId);

    let that = this;
    //
    if (!this.versionFlag) {
      ElMessageBox.confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        console.log("versionId", that.versionId, "CourseId", that.courseId);
        return request({
          url: "/detail",
          method: "post",
          data: {
            versionId: that.versionId,
            courseId: that.courseId,
            departmentId: that.departmentId,
            schoolId: that.schoolId,
          },
        })
          .then(function (res) {
            console.log(res);
            if (res.code == "SUCCESS") {
              ElMessage({
                type: "success",
                message: ``,
                duration: 1500,
              });
              //vesionIdbasecouseId
              that.isVisiable = true;
              that.getDetail();
            }
          })
          .catch((e) => {
            if (e.status === 500) {
              ElMessage({
                type: "error",
                message: ``,
                duration: 1500,
              });
            } else {
              ElMessage({
                type: "error",
                message: `,`,
                duration: 1500,
              });
            }
            that.goBaseCourse();
          });
      });
    } else {
      this.isVisiable = true;
      this.getDetail();
      this.checkVersion();
    }
    console.log("router from", this.$route.query["parentName"]);
    if (this.$route.query["parentName"] == "courseInMajor") {
      this.from = this.$route.query["parentName"];
    } else {
      this.from = "";
    }
    console.log("from", this.from);
  },
  methods: {
    //ID
    //versionIdcourseIddetailId 
    getDetail() {
      let that = this;
      return request({
        url:
          "/detail/list" +
          "?vesionId=" +
          this.versionId +
          "&courseId=" +
          this.courseId,
        method: "get",
      }).then(function (res) {
        // console.log("getDetail:", res);
        res.rows.forEach(function (detail) {
          if (detail.versionId == that.versionId) {
            that.detailId = detail.detailId;
          }
        });
        // console.log("detailId", that.detailId);
        let array = [];
        let obj = {};
        obj.param = that.detailId;
        obj.type = "syllabusFile";
        array.push(obj);
        // that.action = "https://jxjk.hdu.edu.cn/prod-api/common/upload/files";
        // that.action = "https://jxjk.hdu.edu.cn/prod-api/common/upload/files";
        that.action = that.url + "common/upload/files";
        that.data = array[0];
        that.$store.commit("course/setDetailId", that.detailId);
        that.checkFileList();
      });
    },
    //
    backBaseCourse() {
      console.log("from:", this.from);
      if (this.from == "courseInMajor") {
        this.$store.commit("navInfo/setActiveDisplay2", 1);

        this.$router.push("/courses");
      } else {
        this.$router.push("/baseCourse");
      }
    },
    toActivities() {
      this.$router.push("/baseCourseActivities");
    },
    toPaperAnalysis() {
      this.$router.push("/paperAnalysis");
    },
    toIndicators() {
      this.$router.push("/baseCourseIndicators");
    },
    toObjectives() {
      this.$router.push("/baseCourseObjectives");
    },
    toTemplate() {
      this.$router.push("/templateManage");
    },
    //
    getRouter() {
      this.versionName = this.$store.state.course.baseCourseVersionName;
      this.versionId = this.$store.state.course.baseCourseVersionId;
      this.versionFlag = this.$store.state.course.baseCourseVersionFlag;
      this.courseId = this.$store.state.course.baseCourseCourseId;
      this.form.courseName = this.$store.state.course.baseCourseCourseName;
      this.form.courseCode = this.$store.state.course.baseCourseCourseCode;
      this.form.courseType = this.$store.state.course.baseCourseCourseType;
      this.form.courseNature = this.$store.state.course.baseCourseCourseNature;
      this.form.credit = this.$store.state.course.baseCourseCredit;
      this.form.courseYear = this.$store.state.course.baseCourseCourseYear;
      this.form.remark = this.$store.state.course.baseCourseRemark;
      this.departmentId = this.$store.state.currentInfo.departmentId;
      this.schoolId = this.$store.state.currentInfo.schoolId;
    },
    //
    checkVersion() {
      getVersion().then((res) => {
        console.log("checkVersion", res);
        this.versionList = res.data;
      });
    },
    //
    uploadSuccess(response, file, fileList) {
      console.log("response", response, file, "file", fileList, "fileList");
      if (response.code == "SUCCESS") {
        ElMessage({
          type: "success",
          message: ``,
          duration: 1500,
        });
        this.checkFileList();
      }
    },
    //
    uploadExceed() {
      ElMessage({
        type: "warning",
        message: ``,
        duration: 1500,
      });
    },
    //
    beforeRemove(row) {
      ElMessageBox.confirm(``, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.fileList = [];
          this.uploadRemove(row);
        })
        .catch(() => {});
    },
    // 
    uploadRemove(row) {
      removeFileId(row.fileId).then((res) => {
        console.log("removeDetail", res);
        if (res.code === "SUCCESS") {
          ElMessage({
            type: "success",
            message: ``,
            duration: 1500,
          });
          this.checkFileList();
        }
      });
    },
    //
    beforeUpload(file) {
      if (file.size === 0) {
        return ElMessage({
          type: "warning",
          message: ``,
          duration: 1500,
        });
      }
    },

    //
    checkFileList() {
      getFilesList(this.detailId).then((res) => {
        this.filesList = res.data;
        if (this.filesList.length == 0) {
          this.hasFile = false;
        } else {
          this.hasFile = true;
        }
      });
    },
    //
    downloadFile(row) {
      downloadFile(row.fileAddress).then((res) => {
        // console.log("downloadFile", res);
        const blob = new Blob([res]);
        // console.log("blob", blob);
        // saveAs(blob, this.objectInfo.fileName)
        const link = document.createElement("a");
        link.download = decodeURI(row.originalName);
        link.style.display = "none";
        link.href = URL.createObjectURL(blob);
        document.body.appendChild(link);
        link.click();
        URL.revokeObjectURL(link.href);
        document.body.removeChild(link);
      });
    },
    //
    openDrawer() {
      this.drawer = true;
      this.getEditDetail();
    },
    //
    copyDetail(row) {
      console.log("copyDetail", row);
      this.currentRow = row;
      // 
      this.dialogVisible = true;
    },
    //
    getEditDetail() {
      getEditDetail(
        this.keyword,
        this.version,
        this.pageSize,
        this.pageNum
      ).then((res) => {
        console.log("getEditDetail", res);
        this.copyCourseList = res.rows;
        this.total = res.total;
        if (this.pageSize >= res.total) {
          this.showLoadmore = false;
        } else {
          this.showLoadmore = true;
        }
      });
    },
    //
    loadMore() {
      if (this.total - this.pageSize >= 20) {
        this.pageSize += 20;
        this.getEditDetail();
      } else {
        this.pageSize += this.total - this.pageSize;
        this.getEditDetail();
      }
    },
    //
    getSearchValue(data) {
      this.keyword = data;
      this.getEditDetail();
    },
    //
    copyInfo() {
      let obj = {};
      obj.sourceDetailId = this.currentRow.detailId;
      obj.targetDetailId = this.detailId;
      // 
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyActivities = 1;
      } else {
        obj.isCopyActivities = 0;
      }
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyObjectives = 1;
      } else {
        obj.isCopyObjectives = 0;
      }
      if (this.checkList.indexOf("") !== -1) {
        obj.isCopyFormPreset = 1;
      } else {
        obj.isCopyFormPreset = 0;
      }
      copyActAndObj(obj).then((res) => {
        console.log("copyActAndObj", res);
        this.dialogVisible = false;
        if (res.code == "SUCCESS") {
          ElMessage({
            type: "success",
            message: ``,
            duration: 1500,
          });
          this.drawer = false;
        }
      });
    },
  },
};
</script>

