import request from '@/utils/request/request'
//
export function getClass(academicYear,semester,departmentId,schoolId,pageSize,pageNum,KeyWord,status) {
    return request({
      url: '/classes/list',
      method: 'get',
      params:{
        academicYear:academicYear,
        semester:semester,
        departmentId:departmentId,
        schoolId:schoolId,
        pageSize:pageSize,
        pageNum:pageNum,
        selectKeyWord:KeyWord,
        status:status
      }
    })
  }
 //
 export function addClass(academicYear,semester,departmentId,className,identifier,teacherNumber,courseCode,remark,schoolId) {
  return request({
    url: '/classes/add',
    method: 'POST',
    data:{
      academicYear:academicYear,
      semester:semester,
      departmentId:departmentId,
      // 
      className:className,
      // 
      identifier:identifier,
      // 
      teacherNumber:teacherNumber,
      // 
      courseCode:courseCode,
      remark:remark,
      schoolId:schoolId,
    }
  })
}
  //
  export function editClass(array) {
    return request({
      url: '/classes/edit',
      method: 'POST',
      data:array,
    })
}
//
export function setPermission(array) {
  return request({
    url: '/classes/setClassIsRespondent',
    method: 'POST',
    data:array,
  })
}
//
export function deleteClass(classId) {
  return request({
    url: '/classes/'+classId,
    method: 'DELETE',
  })
}
//
export function getClassInfo(classId) {
  return request({
    url: '/classes/'+classId,
    method: 'GET',
  })
}
//
export function associateCourses(array) {
  return request({
    url: '/classes/associateCourse',
    method: 'POST',
    data:array,
  })
}
//
export function submit(classId) {
  return request({
    url: '/classes/submit/'+classId,
    method: 'POST',
  })
}
//or
export function submitFeedback(classId,status){
  return request({
    url:'classes/review/'+classId+'/'+status,
    method:'POST',
  })
}
//
export function createReview(classId,checkFeedback,resultStatus){
  return request({
    url:'/review',
    method:'POST',
    data:{
      'classId':classId,
      'remark':checkFeedback.message,
      'resultStatus':resultStatus
      // 'reviewerName':checkFeedback.reviewerName
    },
  })
}
//
export function getReview(classId){
  return request({
    url:'/review/list',
    method:'GET',
    params:{
      'classId':classId,
    },
  })
}
//
export function deleteReview(reviewId){
  return request({
    url:'/review/'+reviewId,
    method:'DELETE'
  })
}
// 
export function downloadReport(classId){
  return request({
    url:'/classes/exportReport/'+classId,
    method:'POST',
    responseType: 'blob',
  })
}