import FcDesigner from "@form-create/designer";
import Cookies from "js-cookie";
import upload from "./upload.vue";
import { localeProps, makeRequiredRule } from "./utils";
import { shallowRef } from "vue";
const label = " ";
const name = "upload";
let i = 1;
const uniqueId = () => `uni${i++}`;
export const uploadRule = {
  //
  icon: "icon-upload",
  //
  label,
  // key
  name: upload,
  //
  rule() {
    // props  rule , rule 
    return {
      component: shallowRef(upload),
      //
      type: name,
      //field ,
      field: uniqueId(),
      title: label,
      info: "",
      effect: {
        fetch: "",
      },
      //props,  props 
      props: {
        action: "http://81.68.103.96:8099/common/upload/files",
        headers: { Authorization: "Bearer " + Cookies.get("Admin-Token") },
        data: [
          {
            // param: id,W
            type: "workbookFile",
          },
        ],
        listType: 'text',

        onSuccess(res) {
          console.log("suc", res);
        },
      },
    };
  },
  //(props)
  props(_, { t }) {
    return localeProps(t, name + ".props", [
      makeRequiredRule(),
      { type: "switch", field: "multiple", title: "" },
      {
        type: "switch",
        field: "disabled",
        title: "",
      },
      {
        type: "inputNumber",
        field: "limit",
        title: "",
        props: { min: 0 },
      },
    ]);
  },
};
