import request from "@/utils/request/request";
//
export function checkClasses(
  schoolId,
  departmentId,
  keyword,
  academicYear,
  semester,
  status
) {
  return request({
    url: "/classes/teacher",
    method: "get",
    params: {
      schoolId: schoolId,
      departmentId: departmentId,
      selectKeyWord: keyword,
      academicYear,
      semester,
      status,
    },
  });
}
